"use client";

import { useState, useRef } from "react";
import Badge from "@/src/components/base/Badge";
import {
  Download,
  FileText,
  FileArchive,
  Loader2,
} from "lucide-react";

type FileType = "PDF" | "ZIP" | "DOC" | "PPT";

type DownloadCardProps = {
  title: string;
  description?: string;
  fileType?: FileType;
  fileSize?: string;
  driveFileId: string;
};

const fileIcons: Record<FileType, JSX.Element> = {
  PDF: <FileText className="text-red-600" size={26} />,
  ZIP: <FileArchive className="text-yellow-600" size={26} />,
  DOC: <FileText className="text-blue-600" size={26} />,
  PPT: <FileText className="text-orange-600" size={26} />,
};

const DownloadCard = ({
  title,
  description,
  fileType = "PDF",
  fileSize,
  driveFileId,
}: DownloadCardProps) => {
  const downloadUrl = `https://drive.google.com/uc?export=download&id=${driveFileId}`;

  const [isDownloading, setIsDownloading] = useState(false);
  const [elapsed, setElapsed] = useState(0);
  const timerRef = useRef<NodeJS.Timeout | null>(null);
  const startTimeRef = useRef<number>(0);

  const handleDownload = () => {
    if (isDownloading) return;

    setIsDownloading(true);
    startTimeRef.current = performance.now();

    timerRef.current = setInterval(() => {
      setElapsed(
        (performance.now() - startTimeRef.current) / 1000
      );
    }, 100);

    const link = document.createElement("a");
    link.href = downloadUrl;
    link.download = "";
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);

    setTimeout(() => {
      if (timerRef.current) clearInterval(timerRef.current);
      setIsDownloading(false);
    }, 3000);
  };

  return (
    <div className="group rounded-2xl border border-black/10 bg-white shadow-sm hover:shadow-lg transition p-5 flex flex-col gap-4">
      
      {/* Header */}
      <div className="flex items-start gap-4">
        <div className="rounded-xl bg-black/5 p-3">
          {fileIcons[fileType]}
        </div>

        <div className="flex-1">
          <div className="flex items-center justify-between gap-2">
            <h3 className="font-semibold text-gray-900 leading-snug">
              {title}
            </h3>
            <Badge>{fileType}</Badge>
          </div>

          {description && (
            <p className="mt-1 text-sm text-gray-600 line-clamp-2">
              {description}
            </p>
          )}
        </div>
      </div>

      {/* Footer */}
      <div className="flex items-center justify-between border-t pt-3">
        <span className="text-xs text-gray-500">
          {fileSize ? `Size: ${fileSize}` : "Instant download"}
        </span>

        <button
          onClick={handleDownload}
          disabled={isDownloading}
          className="inline-flex items-center gap-2 rounded-xl bg-black px-4 py-2 text-sm font-medium text-white hover:bg-black/90 transition disabled:opacity-70"
        >
          {isDownloading ? (
            <>
              <Loader2 size={16} className="animate-spin" />
              {elapsed.toFixed(1)}s
            </>
          ) : (
            <>
              <Download size={16} />
              Download
            </>
          )}
        </button>
      </div>
    </div>
  );
};

export default DownloadCard;
